<template>
    <div>
        <el-tree default-expand-all :data="all_menu_list" :props="roleMenuDefaultProps"></el-tree>
    </div>
</template>

<script>
import { get_all_menu_api } from '@/api/system';
import { getTrees } from '@/lib/utils';

export default {
    data(){
        return {
            roleMenuDefaultProps: {
                children: 'children',
                label: 'name'
            },
            all_menu_list: []
        }
    },
    created() {
        get_all_menu_api().then(res => {
            this.all_menu_list = getTrees(res,-1);
        })
    }
}
</script>

